'use client';

import React, { useState, useRef, useEffect } from 'react';
import { useNetwork } from '@/context/NetworkContext';
import { Device } from '@/types/network';

const NetworkMap = () => {
  const { devices, connections, selectedDevice, setSelectedDevice } = useNetwork();
  const [zoom, setZoom] = useState(1);
  const [pan, setPan] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const svgRef = useRef<SVGSVGElement>(null);

  const getDeviceColor = (device: Device) => {
    switch (device.status) {
      case 'online':
        return '#22c55e'; // green-500
      case 'offline':
        return '#ef4444'; // red-500
      case 'maintenance':
        return '#f59e0b'; // amber-500
      default:
        return '#6b7280'; // gray-500
    }
  };

  const getDeviceShape = (type: Device['type']) => {
    switch (type) {
      case 'Router':
        return 'rect';
      case 'Switch':
        return 'rect';
      case 'Firewall':
        return 'diamond';
      case 'Server':
        return 'rect';
      case 'Access Point':
        return 'circle';
      default:
        return 'circle';
    }
  };

  const handleMouseDown = (e: React.MouseEvent) => {
    setIsDragging(true);
    setDragStart({ x: e.clientX - pan.x, y: e.clientY - pan.y });
  };

  const handleMouseMove = (e: React.MouseEvent) => {
    if (isDragging) {
      setPan({
        x: e.clientX - dragStart.x,
        y: e.clientY - dragStart.y
      });
    }
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  const handleWheel = (e: React.WheelEvent) => {
    e.preventDefault();
    const delta = e.deltaY > 0 ? 0.9 : 1.1;
    setZoom(prev => Math.max(0.1, Math.min(3, prev * delta)));
  };

  const renderDevice = (device: Device) => {
    const x = (device.position?.x || 0) * zoom + pan.x;
    const y = (device.position?.y || 0) * zoom + pan.y;
    const isSelected = selectedDevice?.id === device.id;
    const shape = getDeviceShape(device.type);
    const color = getDeviceColor(device);

    const commonProps = {
      onClick: () => setSelectedDevice(device),
      className: "cursor-pointer hover:opacity-80 transition-opacity",
      stroke: isSelected ? '#000' : '#374151',
      strokeWidth: isSelected ? 3 : 1,
      fill: color
    };

    let shapeElement;
    
    if (shape === 'rect') {
      shapeElement = (
        <rect
          x={x - 20}
          y={y - 15}
          width={40}
          height={30}
          rx={4}
          {...commonProps}
        />
      );
    } else if (shape === 'diamond') {
      const points = `${x},${y-20} ${x+20},${y} ${x},${y+20} ${x-20},${y}`;
      shapeElement = (
        <polygon
          points={points}
          {...commonProps}
        />
      );
    } else {
      shapeElement = (
        <circle
          cx={x}
          cy={y}
          r={20}
          {...commonProps}
        />
      );
    }

    return (
      <g key={device.id}>
        {shapeElement}
        <text
          x={x}
          y={y + 35}
          textAnchor="middle"
          className="font-sans text-xs fill-gray-700 pointer-events-none"
        >
          {device.name}
        </text>
        <text
          x={x}
          y={y + 48}
          textAnchor="middle"
          className="font-sans text-xs fill-gray-500 pointer-events-none"
        >
          {device.ip}
        </text>
      </g>
    );
  };

  const renderConnection = (connection: any) => {
    const source = devices.find(d => d.id === connection.sourceId);
    const target = devices.find(d => d.id === connection.targetId);
    
    if (!source || !target) return null;

    const x1 = (source.position?.x || 0) * zoom + pan.x;
    const y1 = (source.position?.y || 0) * zoom + pan.y;
    const x2 = (target.position?.x || 0) * zoom + pan.x;
    const y2 = (target.position?.y || 0) * zoom + pan.y;

    const lineColor = connection.type === 'fiber' ? '#3b82f6' : '#6b7280';
    const strokeWidth = connection.type === 'fiber' ? 3 : 2;

    return (
      <g key={connection.id}>
        <line
          x1={x1}
          y1={y1}
          x2={x2}
          y2={y2}
          stroke={lineColor}
          strokeWidth={strokeWidth}
          strokeDasharray={connection.type === 'wireless' ? '5,5' : 'none'}
          className="pointer-events-none"
        />
        {connection.label && (
          <text
            x={(x1 + x2) / 2}
            y={(y1 + y2) / 2 - 5}
            textAnchor="middle"
            className="font-sans text-xs fill-gray-600 pointer-events-none"
          >
            {connection.label}
          </text>
        )}
      </g>
    );
  };

  if (devices.length === 0) {
    return (
      <div className="flex items-center justify-center h-full bg-gray-50 rounded-lg border-2 border-dashed border-gray-300">
        <div className="text-center">
          <p className="text-gray-500 text-lg mb-2">No devices found</p>
          <p className="text-gray-400 text-sm">Add devices to visualize your network</p>
        </div>
      </div>
    );
  }

  return (
    <div className="relative w-full h-full bg-white rounded-lg border shadow-sm overflow-hidden">
      {/* Controls */}
      <div className="absolute top-4 right-4 z-10 flex flex-col gap-2">
        <button
          onClick={() => setZoom(prev => Math.min(3, prev * 1.2))}
          className="px-3 py-1 bg-white border rounded shadow-sm hover:bg-gray-50 text-sm font-medium"
        >
          Zoom In
        </button>
        <button
          onClick={() => setZoom(prev => Math.max(0.1, prev * 0.8))}
          className="px-3 py-1 bg-white border rounded shadow-sm hover:bg-gray-50 text-sm font-medium"
        >
          Zoom Out
        </button>
        <button
          onClick={() => {
            setZoom(1);
            setPan({ x: 0, y: 0 });
          }}
          className="px-3 py-1 bg-white border rounded shadow-sm hover:bg-gray-50 text-sm font-medium"
        >
          Reset
        </button>
      </div>

      {/* Legend */}
      <div className="absolute top-4 left-4 z-10 bg-white border rounded shadow-sm p-3">
        <h4 className="font-medium text-sm mb-2">Legend</h4>
        <div className="space-y-1 text-xs">
          <div className="flex items-center gap-2">
            <div className="w-3 h-3 bg-green-500 rounded"></div>
            <span>Online</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-3 h-3 bg-red-500 rounded"></div>
            <span>Offline</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-3 h-3 bg-amber-500 rounded"></div>
            <span>Maintenance</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-4 h-1 bg-blue-500"></div>
            <span>Fiber</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-4 h-1 bg-gray-500"></div>
            <span>Ethernet</span>
          </div>
        </div>
      </div>

      {/* SVG Canvas */}
      <svg
        ref={svgRef}
        width="100%"
        height="100%"
        className="cursor-grab active:cursor-grabbing"
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
        onWheel={handleWheel}
      >
        {/* Render connections first (behind devices) */}
        {connections.map(renderConnection)}
        
        {/* Render devices */}
        {devices.map(renderDevice)}
      </svg>

      {/* Device Details Panel */}
      {selectedDevice && (
        <div className="absolute bottom-4 right-4 z-10 bg-white border rounded-lg shadow-lg p-4 w-80">
          <div className="flex justify-between items-start mb-3">
            <h3 className="font-semibold text-lg">{selectedDevice.name}</h3>
            <button
              onClick={() => setSelectedDevice(null)}
              className="text-gray-400 hover:text-gray-600 text-xl leading-none"
            >
              ×
            </button>
          </div>
          <div className="space-y-2 text-sm">
            <div className="flex justify-between">
              <span className="font-medium">Type:</span>
              <span>{selectedDevice.type}</span>
            </div>
            <div className="flex justify-between">
              <span className="font-medium">IP Address:</span>
              <span className="font-mono">{selectedDevice.ip}</span>
            </div>
            <div className="flex justify-between">
              <span className="font-medium">Status:</span>
              <span className={`px-2 py-1 rounded text-xs font-medium ${
                selectedDevice.status === 'online' ? 'bg-green-100 text-green-800' :
                selectedDevice.status === 'offline' ? 'bg-red-100 text-red-800' :
                'bg-amber-100 text-amber-800'
              }`}>
                {selectedDevice.status.charAt(0).toUpperCase() + selectedDevice.status.slice(1)}
              </span>
            </div>
            {selectedDevice.location && (
              <div className="flex justify-between">
                <span className="font-medium">Location:</span>
                <span>{selectedDevice.location}</span>
              </div>
            )}
            {selectedDevice.vendor && (
              <div className="flex justify-between">
                <span className="font-medium">Vendor:</span>
                <span>{selectedDevice.vendor}</span>
              </div>
            )}
            {selectedDevice.model && (
              <div className="flex justify-between">
                <span className="font-medium">Model:</span>
                <span>{selectedDevice.model}</span>
              </div>
            )}
            {selectedDevice.description && (
              <div className="pt-2 border-t">
                <span className="font-medium">Description:</span>
                <p className="text-gray-600 mt-1">{selectedDevice.description}</p>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default NetworkMap;
