'use client';

import React, { useState } from 'react';
import { useNetwork } from '@/context/NetworkContext';
import { Device } from '@/types/network';
import AddDeviceModal from './AddDeviceModal';

const NetworkList = () => {
  const { devices, stats, selectedDevice, setSelectedDevice, searchDevices } = useNetwork();
  const [searchQuery, setSearchQuery] = useState('');
  const [filterStatus, setFilterStatus] = useState<'all' | 'online' | 'offline' | 'maintenance'>('all');
  const [filterType, setFilterType] = useState<'all' | Device['type']>('all');
  const [showAddModal, setShowAddModal] = useState(false);

  const getStatusColor = (status: Device['status']) => {
    switch (status) {
      case 'online':
        return 'bg-green-100 text-green-800';
      case 'offline':
        return 'bg-red-100 text-red-800';
      case 'maintenance':
        return 'bg-amber-100 text-amber-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getTypeIcon = (type: Device['type']) => {
    switch (type) {
      case 'Router':
        return 'R';
      case 'Switch':
        return 'S';
      case 'Firewall':
        return 'F';
      case 'Server':
        return 'SV';
      case 'Access Point':
        return 'AP';
      default:
        return '?';
    }
  };

  const filteredDevices = devices.filter(device => {
    const matchesSearch = searchQuery === '' || 
      device.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      device.ip.includes(searchQuery) ||
      device.location?.toLowerCase().includes(searchQuery.toLowerCase());
    
    const matchesStatus = filterStatus === 'all' || device.status === filterStatus;
    const matchesType = filterType === 'all' || device.type === filterType;
    
    return matchesSearch && matchesStatus && matchesType;
  });

  return (
    <div className="h-full flex flex-col bg-gray-50 border-r">
      {/* Header */}
      <div className="p-4 border-b bg-white">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-lg font-semibold">Network Devices</h2>
          <button
            onClick={() => setShowAddModal(true)}
            className="px-4 py-2 bg-blue-600 text-white text-sm font-medium rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors"
          >
            Add Device
          </button>
        </div>
        
        {/* Stats */}
        <div className="grid grid-cols-2 gap-2 mb-4">
          <div className="bg-green-50 p-2 rounded text-center">
            <div className="text-lg font-bold text-green-600">{stats.onlineDevices}</div>
            <div className="text-xs text-green-600">Online</div>
          </div>
          <div className="bg-red-50 p-2 rounded text-center">
            <div className="text-lg font-bold text-red-600">{stats.offlineDevices}</div>
            <div className="text-xs text-red-600">Offline</div>
          </div>
          <div className="bg-amber-50 p-2 rounded text-center">
            <div className="text-lg font-bold text-amber-600">{stats.maintenanceDevices}</div>
            <div className="text-xs text-amber-600">Maintenance</div>
          </div>
          <div className="bg-blue-50 p-2 rounded text-center">
            <div className="text-lg font-bold text-blue-600">{stats.totalDevices}</div>
            <div className="text-xs text-blue-600">Total</div>
          </div>
        </div>

        {/* Search */}
        <div className="mb-3">
          <input
            type="text"
            placeholder="Search devices..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full px-3 py-2 border rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
        </div>

        {/* Filters */}
        <div className="space-y-2">
          <div>
            <label className="block text-xs font-medium text-gray-700 mb-1">Status</label>
            <select
              value={filterStatus}
              onChange={(e) => setFilterStatus(e.target.value as any)}
              className="w-full px-2 py-1 border rounded text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all">All Status</option>
              <option value="online">Online</option>
              <option value="offline">Offline</option>
              <option value="maintenance">Maintenance</option>
            </select>
          </div>
          <div>
            <label className="block text-xs font-medium text-gray-700 mb-1">Type</label>
            <select
              value={filterType}
              onChange={(e) => setFilterType(e.target.value as any)}
              className="w-full px-2 py-1 border rounded text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all">All Types</option>
              <option value="Router">Router</option>
              <option value="Switch">Switch</option>
              <option value="Firewall">Firewall</option>
              <option value="Server">Server</option>
              <option value="Access Point">Access Point</option>
              <option value="Other">Other</option>
            </select>
          </div>
        </div>
      </div>

      {/* Device List */}
      <div className="flex-1 overflow-y-auto">
        {filteredDevices.length === 0 ? (
          <div className="p-4 text-center text-gray-500">
            <p className="text-sm">No devices found</p>
            {searchQuery && (
              <p className="text-xs mt-1">Try adjusting your search or filters</p>
            )}
          </div>
        ) : (
          <div className="p-2 space-y-1">
            {filteredDevices.map((device) => (
              <div
                key={device.id}
                onClick={() => setSelectedDevice(device)}
                className={`p-3 rounded-lg cursor-pointer transition-all duration-200 ${
                  selectedDevice?.id === device.id
                    ? 'bg-blue-100 border-2 border-blue-300 shadow-sm'
                    : 'bg-white border border-gray-200 hover:bg-gray-50 hover:shadow-sm'
                }`}
              >
                <div className="flex items-start justify-between mb-2">
                  <div className="flex items-center gap-2">
                    <div className="w-8 h-8 bg-gray-200 rounded-full flex items-center justify-center text-xs font-bold text-gray-600">
                      {getTypeIcon(device.type)}
                    </div>
                    <div>
                      <h3 className="font-medium text-sm text-gray-900 truncate">
                        {device.name}
                      </h3>
                      <p className="text-xs text-gray-500 font-mono">{device.ip}</p>
                    </div>
                  </div>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(device.status)}`}>
                    {device.status}
                  </span>
                </div>
                
                <div className="space-y-1">
                  <div className="flex justify-between text-xs">
                    <span className="text-gray-500">Type:</span>
                    <span className="text-gray-700">{device.type}</span>
                  </div>
                  {device.location && (
                    <div className="flex justify-between text-xs">
                      <span className="text-gray-500">Location:</span>
                      <span className="text-gray-700 truncate ml-2">{device.location}</span>
                    </div>
                  )}
                  {device.vendor && (
                    <div className="flex justify-between text-xs">
                      <span className="text-gray-500">Vendor:</span>
                      <span className="text-gray-700">{device.vendor}</span>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Results Count */}
      <div className="p-3 border-t bg-white">
        <p className="text-xs text-gray-500 text-center">
          Showing {filteredDevices.length} of {devices.length} devices
        </p>
      </div>

      {/* Add Device Modal */}
      {showAddModal && (
        <AddDeviceModal onClose={() => setShowAddModal(false)} />
      )}
    </div>
  );
};

export default NetworkList;
